.main_image img{
  border-radius: 20px;
  width: 100%;
  height: 220px;
}
.main{
  padding: 20px 0 20px 20px;
}
.main_posts{
  padding: 20px 0 20px 10px;
}
.main_flex{
  display: flex;
}
.icon{
  flex:0 0 100px;
}
.icon img{
  max-width: 100%;
}
.main_descripttion{
  padding: 10px 0 0 30px;
}
.posts_img img{
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.posts{
  display: flex;
  align-items: center;
}

.add_post{
  margin: 5px 0;
  display: block;
}
.info_error{
  position: relative;
  bottom: 15px;
  left: 10px;
  color: red
}
.formControl{
  border: 2px solid red;
}
