.sidebar_Irow{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.item_img{
  position: relative;
  width: 50px;
  height: 50px;
  background-color: brown;
  border-radius: 50%;
}
.item_img img{
  position: absolute;
  width: 100%;
  height: 100%;
  left:0;
  top:0;
  border-radius: 50%;
}