.header {
  position: relative;
  width: 100%;
  height: 70px;
  margin: 20px 0 10px 0;
  background-color: rgba(204, 204, 204, 0.705);
  border-radius: 20px;
  border: 2px solid rgba(0, 0, 0, 0.288);
}
.header_row{
  display: flex;
  align-items: center;
  padding: 10px 0;
}
.login{
  position: absolute;
  right: 20px;
  top: 15px;
}
.info_user{
  display: flex;
  align-items: center;
}
.user_image{
  margin: 0 0 0 10px;
  padding: 5px 0 0 0;
}
.user_image img{
  width: 30px;
  height: 30px;
  border-radius: 20%;
}
.header_logo {
  margin: 0 0 0 35px;
}
.header_logo img {
  position: relative;
  width: 50px;
  height: 50px;
}
