.find_title{
  font-size: 30px;
  margin: 0 0 10px 0;
}
.find_row{
  display: flex;
  flex-direction: column;
}
.find_columns{
  margin: 0 0 30px 0;
}
.find_item_row{
  position: relative;
  display: flex;
  width: 650px;
}

.find_follow{
  position: absolute;
  right: -150px;
  display: inline-block;
  height: 44px;
	line-height: 40px;
	padding: 0 26px;
  align-self: center;
  background:transparent;
	border: 2px solid #ccc;
	font-size: 20px;
	border-radius: 15px;
  outline: none;
}
.item_status{
  margin: -20px 0 0 0;
}
.item_img{
  margin: 0 5px 0 0;
}
.item_img img{
	width: 70px;
  height: 70px;
  border-radius: 50%;

}
.item_body {
	display: flex;
	padding: 15px;
	border: 2px solid #ccc;
	border-radius: 15px;
  flex:1 1 100%;
}
.item_info{
  display: flex;
  flex-direction: column;
  padding: 0 40px 0 0;
}
.item_info:first-child{
  flex:1 1 auto;
}
.item_info:last-child{
  text-align: right;
  padding: 0;
}
/* .pageNavigate{

} */
.pageNavigate span{
  margin: 0 2px;
  cursor: pointer;
}
.pageActive{
  font-size: 25px;
}